import React, { Component } from "react";
import { Row, Col, Button, Select } from 'antd';
import Icon from "../icon";
import './style.less'
const typeOptions = [
  { label: '字符串', value: 'string' },
  { label: '数值', value: 'number' },
  { label: '布尔值', value: 'boolean' },
]

class View extends Component {
  handleChange = (index, name, e) => {
    let data = Array.from(this.props.value)
    if (data[index]) {
      console.warn(e);
      data[index][name] = e
    }
    this.onChange(data)
  }

  onChange = data => {
    const { onChange } = this.props
    if (onChange) onChange(data)
  };

  add = () => {
    let data = Array.from(this.props.value || [])
    data.push({ key: '', value: '' })
    this.onChange(data)
  }

  remove = index => {
    let data = Array.from(this.props.value || [])
    data.splice(index, 1)
    this.onChange(data)
  }

  render() {
    const { value = [], keyOptions = [], valOptions = [],
      keyTitle = '键名', valueTitle = '值', btnText = '添加' } = this.props
    return (
      <div className="query-fields">
        {
          value.map((item, index) => (
            <Row key={index} style={{ marginBottom: 5 }}>
              <Col flex="auto">
                <Row gutter={[8, 8]}>
                  <Col span={9}>
                    <Select
                      options={keyOptions}
                      placeholder={keyTitle}
                      value={item.key || undefined}
                      onChange={this.handleChange.bind(this, index, 'key')}
                    />
                  </Col>
                  <Col span={9}>
                    <Select
                      options={valOptions}
                      placeholder={valueTitle}
                      value={item.value || undefined}
                      onChange={this.handleChange.bind(this, index, 'value')}
                    />
                  </Col>
                  <Col span={6}>
                    <Select
                      options={typeOptions}
                      placeholder="数据类型"
                      value={item.type || undefined}
                      onChange={this.handleChange.bind(this, index, 'type')}
                    />
                  </Col>
                </Row>
              </Col>
              <Col flex="30px">
                <Button type="text" shape="circle" danger
                  icon={<Icon iconName={'CalculatorMultiply'} />}
                  onClick={this.remove.bind(this, index)}
                />
              </Col>
            </Row>
          ))
        }
        <Button type="dashed" onClick={this.add} style={{ width: '100%' }}
          icon={<Icon iconName={'Add'} marginRight={5} color="#007eff" />}
        >
          {btnText}
        </Button>
      </div>
    );
  }
}
export default View